* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(to left, #333, #333 50%, #eee 100%);
  color: white;
  min-height: 100vh;
  font-family: 'Fira Sans', sans-serif;
}

.App {
  padding: 0 25%;
}

header {
  font-size: 2rem;
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo {
  position: relative;
  background-color: black;
  padding: 16px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  transition: 0.5s;
  cursor: pointer;
  margin-bottom: 1rem;
}

.todo:hover {
  opacity: 0.7;
}

.todo .checkbox {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 1rem;
  border-radius: 999px;
  background-color: #333;
  transition: 0.2s;
}

.todo.is-complete .checkbox {
  background-color: #eee;
}

.todo.is-complete .text {
  text-decoration: line-through;
}

.todo .delete-todo {
  position: absolute;
  right: 1rem;
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.addPopup {
  position: fixed;
  bottom: 5rem;
  right: 5rem;
  font-size: 2rem;
  font-weight: 900;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  cursor: pointer;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 25rem;
  background-color: rgb(209, 209, 209);
  padding: 2rem;
  border-radius: 8px;
  color: black;
}

.closePopup {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup h3 {
  margin-bottom: 2rem;
  font-weight: 400;
}

.add-todo-input {
  appearance: none;
  border: none;
  outline: none;
  padding: 1.5rem;
  border-radius: 8px;
  width: 100%;
  font-size: 1.2rem;
}

.button {
  padding: 1rem 2rem;
  display: inline-block;
  font-weight: 700;
  font-size: 1.2rem;
  margin-top: 2rem;
  background-color: black;
  color: white;
  border-radius: 8px;
  text-align: center;
  cursor: pointer;
}